.Icon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    font-size: 24px;
    font-weight: bold;
    color: #FFFFFF;
}
.Icon-container-check{
    position: absolute;
    left: 12px;
}
.Icon-container-check--active{
    color: #4caf50;
}

.Icon-svg{
    width: 42px;
    height: 42px;
}