.CreateTodoButton{
    background-color: #D52459;
    box-shadow: 0px 5px 25px rgba(255, 0, 89, 0.5);
    color: #000000;
    margin: 22px;
    padding: 22px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 64px;
    height: 64px;
    font-size: 50px;
    border: none;
    transition: .3s ease;
    cursor: pointer;
    position: fixed;
    bottom: 4px;
    right: 142px;
    z-index: 1;
}
.CreateTodoButton:hover{
    transform: rotate(180deg);
}
.CreateTodoButton:active{
    transform: scale(0.94) translateY(0px);
}

@media screen and (max-width: 780px){
    .CreateTodoButton{
        right: calc(50vw - 32px);
        bottom: 5vh;
        margin: 0;
        padding: 22px 0 24px 0;
    } 
}
