.container-empty {
    box-sizing: border-box;
    width: 490px;
    height: 210px;
}

.content-empty {
    display: block;
    position: sticky;
    overflow: hidden;
    font-family: 'Lato', sans-serif;
    font-size: 36px;
    line-height: 40px;
    color: #ffffff;
    margin-top: 50px;
}

.content__container {
    font-weight: 600;
    font-family: 'Arial';
    overflow: hidden;
    height: 40px;
    padding: 0 40px;
}

.content__container:before {
    content: '[';
    left: 0;
}

.content__container:after {
    content: ']';
    position: absolute;
    right: 0;
}

.content__container:after,
.content__container:before {
    position: absolute;
    top: -2px;
    color: #24678D;
    font-size: 36px;
    line-height: 40px;
    -webkit-animation-name: opacity;
    -webkit-animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    animation-name: opacity;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

.content__container__text {
    display: inline;
    float: left;
    margin: 0;
}

.content__container__list {
    margin-top: 0;
    padding-left: 310px;
    text-align: left;
    list-style: none;
    -webkit-animation-name: change;
    -webkit-animation-duration: 10s;
    -webkit-animation-iteration-count: infinite;
    animation-name: change;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}

.content__container__list__item {
    line-height: 40px;
    margin: 0;
    font-size: 36px;
    font-weight: 600;
}

@keyframes opacity {

    0%,
    100% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
}

@keyframes change {

    0%,
    12.66%,
    100% {
        transform: translate3d(0, 0, 0);
    }

    16.66%,
    29.32% {
        transform: translate3d(0, -25%, 0);
    }

    33.32%,
    45.98% {
        transform: translate3d(0, -50%, 0);
    }

    49.98%,
    62.64% {
        transform: translate3d(0, -75%, 0);
    }

    66.64%,
    79.3% {
        transform: translate3d(0, -50%, 0);
    }

    83.3%,
    95.96% {
        transform: translate3d(0, -25%, 0);
    }
}

@media (max-width: 768px) {
    .container-empty {
        width: 66vw;
        height: 24vh;
        padding: 0;
    }
    .content__container {
        height: 36px;
        padding: 0 6vw;
    }
    .content__container__text{
        font-size: 20px;
    }
    .content__container__list{
        padding-left: 160px;
    }
    .content__container__list__item{
        font-size: 20px;
    }
}
@media (max-width: 415px) {
    .container-empty {
        width: 70vw;
    }
    .content__container {
        padding: 0 28px;
    }
    .content__container__text{
        font-size: 18px;
    }
    .content__container__list{
        padding-left: 162px;
    }
    .content__container__list__item{
        font-size: 18px;
    }
}